import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import { BodySection, HeaderSection } from '../../Sections';
import { WhiteRendition, GrayRendition } from '../../Renditions';
import { BodyPanel, HeaderPanel, StandardLayout } from '../../StandardLayout';

// Types
import { TestMetadata } from 'types';

// Styles
import './BasicCanvas.scss';

export interface BasicCanvasProps {
  /** header components */
  header?: React.ReactNode;
  /** Additional class */
  className?: string;
  // Test IDs for BasicCanvas
  testMetadata?: {
    standardSection?: TestMetadata;
    headerRendition?: TestMetadata;
    headerSection?: TestMetadata;
    bodyRendition?: TestMetadata;
    bodySection?: TestMetadata;
  };
}

const BasicCanvas: React.FC<BasicCanvasProps> = ({
  header,
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const baseClassNames = useMemo(
    () => classNames('sb-cp-basic-canvas', className),
    [className],
  );

  return (
    <StandardLayout
      {...otherProps}
      className={baseClassNames}
      {...testMetadata?.standardSection}
    >
      {header && (
        <HeaderPanel isScrollable={false}>
          <WhiteRendition {...testMetadata?.headerRendition} elevated>
            <HeaderSection {...testMetadata?.headerSection}>
              {header}
            </HeaderSection>
          </WhiteRendition>
        </HeaderPanel>
      )}
      <BodyPanel>
        <GrayRendition {...testMetadata?.bodyRendition} fullSize>
          <BodySection {...testMetadata?.bodySection}>{children}</BodySection>
        </GrayRendition>
      </BodyPanel>
    </StandardLayout>
  );
};

export default BasicCanvas;
