import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import { BodySection, HeaderSection } from '../../Sections';
import { WhiteRendition } from '../../Renditions';
import {
  BodyPanel,
  FooterPanel,
  HeaderPanel,
  StandardLayout,
} from '../../StandardLayout';

// Types
import { TestMetadata } from 'types';

// Styles
import './AllPanelFormCanvas.scss';

export interface AllPanelFormCanvasProps {
  /** header components */
  header?: React.ReactNode;
  /** footer components */
  footer?: React.ReactNode;
  /** Additional class */
  className?: string;
  // Test IDs for AllPanelFormCanvas
  testMetadata?: {
    standardSection?: TestMetadata;
    headerRendition?: TestMetadata;
    headerSection?: TestMetadata;
    bodyRendition?: TestMetadata;
    bodySection?: TestMetadata;
    footerRendition?: TestMetadata;
    footerSection?: TestMetadata;
  };
}

const AllPanelFormCanvas: React.FC<AllPanelFormCanvasProps> = ({
  header,
  footer,
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const baseClassNames = useMemo(
    () => classNames('sb-cp-all-panel-form-canvas', className),
    [className],
  );

  return (
    <StandardLayout
      {...otherProps}
      className={baseClassNames}
      {...testMetadata?.standardSection}
    >
      {header && (
        <HeaderPanel isScrollable={false}>
          <WhiteRendition {...testMetadata?.headerRendition} elevated>
            <HeaderSection {...testMetadata?.headerSection}>
              {header}
            </HeaderSection>
          </WhiteRendition>
        </HeaderPanel>
      )}
      <BodyPanel>
        <WhiteRendition
          {...testMetadata?.bodyRendition}
          fullSize
          borders="bottom"
        >
          <BodySection {...testMetadata?.bodySection}>{children}</BodySection>
        </WhiteRendition>
      </BodyPanel>
      {footer && (
        <FooterPanel>
          <WhiteRendition {...testMetadata?.footerRendition}>
            <BodySection {...testMetadata?.footerSection}>{footer}</BodySection>
          </WhiteRendition>
        </FooterPanel>
      )}
    </StandardLayout>
  );
};

export default AllPanelFormCanvas;
