import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import { BodySection, FullSection, HeaderSection } from '../../Sections';
import { WhiteRendition, GrayRendition } from '../../Renditions';
import {
  DualPanelLayout,
  PrimaryPanel,
  SecondaryPanel,
} from '../../DualPanelLayout';
import { BodyPanel, HeaderPanel, StandardLayout } from '../../StandardLayout';

// Types
import { TestMetadata } from 'types';

// Styles
import './DualPanelCanvas.scss';

export interface DualPanelCanvasProps {
  /** header components */
  header?: React.ReactNode;
  /** left-hand components */
  secondaryPanel?: React.ReactNode;
  /** Additional class */
  className?: string;
  // Test IDs for System
  testMetadata?: {
    standardSection?: TestMetadata;
    headerRendition?: TestMetadata;
    headerSection?: TestMetadata;
    dualPanel?: TestMetadata;
    rendition?: TestMetadata;
    Section?: TestMetadata;
  };
}

const DualPanelCanvas: React.FC<DualPanelCanvasProps> = ({
  header,
  secondaryPanel,
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const dualPanelClassNames = useMemo(
    () => classNames('sb-cp-dual-panel-canvas', className),
    [className],
  );

  return (
    <StandardLayout {...testMetadata?.standardSection}>
      {header && (
        <HeaderPanel isScrollable={false}>
          <WhiteRendition {...testMetadata?.headerRendition} elevated>
            <HeaderSection {...testMetadata?.headerSection}>
              {header}
            </HeaderSection>
          </WhiteRendition>
        </HeaderPanel>
      )}
      <BodyPanel>
        <DualPanelLayout
          {...otherProps}
          className={dualPanelClassNames}
          {...testMetadata?.dualPanel}
        >
          <SecondaryPanel className="sb-cp-dual-panel-canvas_secondary">
            <GrayRendition
              {...testMetadata?.rendition}
              borders="right"
              fullSize
            >
              <BodySection
                padding={['normal', 'loose', 'normal', 'loose']}
                {...testMetadata?.Section}
              >
                {secondaryPanel}
              </BodySection>
            </GrayRendition>
          </SecondaryPanel>
          <PrimaryPanel className="sb-cp-dual-panel-canvas_primary">
            <WhiteRendition fullSize {...testMetadata?.rendition}>
              <FullSection {...testMetadata?.Section}>{children}</FullSection>
            </WhiteRendition>
          </PrimaryPanel>
        </DualPanelLayout>
      </BodyPanel>
    </StandardLayout>
  );
};

export default DualPanelCanvas;
