import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import Container from '../../Container';

// Types
import { SpacingOptions, TestMetadata } from 'types';

// Styles
import './BodySection.scss';

export interface BodySectionProps {
  /** Controls the padding: none, normal (default), tight, loose*/
  padding?: SpacingOptions | SpacingOptions[];
  /** Additional class */
  className?: string;
  // Test IDs for BodySection
  testMetadata?: TestMetadata;
}

const BodySection: React.FC<BodySectionProps> = ({
  padding = ['normal', 'loose', 'normal', 'loose'],
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const bodySectionClassNames = useMemo(
    () => classNames('sb-body-section', className),
    [className],
  );

  return (
    <Container
      {...otherProps}
      padding={padding}
      className={bodySectionClassNames}
      {...testMetadata}
    >
      {children}
    </Container>
  );
};

export default BodySection;
