import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import { BodySection, HeaderSection } from '../../Sections';
import { WhiteRendition } from '../../Renditions';
import { BodyPanel, HeaderPanel, StandardLayout } from '../../StandardLayout';

// Types
import { TestMetadata } from 'types';

// Styles
import './BasicFormCanvas.scss';

export interface BasicFormCanvasProps {
  /** header components */
  header?: React.ReactNode;
  /** Additional class */
  className?: string;
  // Test IDs for BasicFormCanvas
  testMetadata?: {
    standardSection?: TestMetadata;
    headerRendition?: TestMetadata;
    headerSection?: TestMetadata;
    bodyRendition?: TestMetadata;
    bodySection?: TestMetadata;
  };
}

const BasicFormCanvas: React.FC<BasicFormCanvasProps> = ({
  header,
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const baseClassNames = useMemo(
    () => classNames('sb-cp-basic-form-canvas', className),
    [className],
  );

  return (
    <StandardLayout
      {...otherProps}
      className={baseClassNames}
      {...testMetadata?.standardSection}
    >
      {header && (
        <HeaderPanel isScrollable={false}>
          <WhiteRendition {...testMetadata?.headerRendition} elevated>
            <HeaderSection {...testMetadata?.headerSection}>
              {header}
            </HeaderSection>
          </WhiteRendition>
        </HeaderPanel>
      )}
      <BodyPanel>
        <WhiteRendition {...testMetadata?.bodyRendition} fullSize>
          <BodySection {...testMetadata?.bodySection}>{children}</BodySection>
        </WhiteRendition>
      </BodyPanel>
    </StandardLayout>
  );
};

export default BasicFormCanvas;
