import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import Container from '../../Container';

// Types
import { SpacingOptions, TestMetadata } from 'types';

// Styles
import './FullSection.scss';

export interface FullSectionProps {
  /** Controls the padding: none, normal (default), tight, loose*/
  padding?: SpacingOptions | SpacingOptions[];
  /** Additional class */
  className?: string;
  // Test IDs for FullSection
  testMetadata?: TestMetadata;
}

const FullSection: React.FC<FullSectionProps> = ({
  padding = 'none',
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const fullSectionClassNames = useMemo(
    () => classNames('sb-full-section', className),
    [className],
  );

  return (
    <Container
      {...otherProps}
      padding={padding}
      className={fullSectionClassNames}
      {...testMetadata}
    >
      {children}
    </Container>
  );
};

export default FullSection;
