import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import { BodySection, HeaderSection } from '../../Sections';
import { WhiteRendition } from '../../Renditions';
import { BodyPanel, HeaderPanel, StandardLayout } from '../../StandardLayout';

// Types
import { BorderOptions, TestMetadata } from 'types';

// Styles
import './ComponentCanvas.scss';

export interface ComponentCanvasProps {
  /** header components */
  header?: React.ReactNode;
  /** header border - bottom or none */
  headerBorder?: BorderOptions;
  /** use full height */
  fullSize?: boolean;
  /** Additional class */
  className?: string;
  /** flush */
  flush?: boolean;
  // Test IDs for ComponentCanvas
  testMetadata?: {
    standardSection?: TestMetadata;
    rendition?: TestMetadata;
    headerSection?: TestMetadata;
    bodySection?: TestMetadata;
  };
}

const ComponentCanvas: React.FC<ComponentCanvasProps> = ({
  header,
  headerBorder = 'bottom',
  fullSize,
  children,
  className,
  testMetadata,
  flush = false,
  ...otherProps
}) => {
  const baseClassNames = useMemo(
    () => classNames('sb-cp-component-canvas', className),
    [className],
  );

  return (
    <WhiteRendition
      roundedBorder
      borders={['top', 'left', 'bottom', 'right']}
      fullSize={fullSize}
      {...testMetadata?.rendition}
    >
      <StandardLayout
        {...otherProps}
        className={baseClassNames}
        {...testMetadata?.standardSection}
      >
        {header && (
          <HeaderPanel isScrollable={false}>
            <WhiteRendition borders={headerBorder}>
              <HeaderSection
                {...testMetadata?.headerSection}
                padding={flush ? 'none' : 'normal'}
              >
                {header}
              </HeaderSection>
            </WhiteRendition>
          </HeaderPanel>
        )}
        <BodyPanel>
          <BodySection
            {...testMetadata?.bodySection}
            padding={flush ? 'none' : 'normal'}
          >
            {children}
          </BodySection>
        </BodyPanel>
      </StandardLayout>
    </WhiteRendition>
  );
};

export default ComponentCanvas;
