import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import Container from '../../Container';

// Types
import { SpacingOptions, TestMetadata } from 'types';

// Styles
import './HeaderSection.scss';

export interface HeaderSectionProps {
  /** Controls the padding: none, normal (default), tight, loose*/
  padding?: SpacingOptions | SpacingOptions[];
  /** Additional class */
  className?: string;
  // Test IDs for HeaderSection
  testMetadata?: TestMetadata;
}

const HeaderSection: React.FC<HeaderSectionProps> = ({
  padding = ['normal', 'loose', 'normal', 'loose'],
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const headerSectionClassNames = useMemo(
    () => classNames('sb-header-section', className),
    [className],
  );

  return (
    <Container
      {...otherProps}
      padding={padding}
      className={headerSectionClassNames}
      {...testMetadata}
    >
      {children}
    </Container>
  );
};

export default HeaderSection;
