import { useMemo } from 'react';
import classNames from 'classnames';

// Components

// Types
import { RenditionProps } from '../types';

// Styles
import './TransparentRendition.scss';
import '../Renditions.scss';

const TransparentRendition: React.FC<RenditionProps> = ({
  elevated = false,
  fullSize = false,
  borders = 'none',
  roundedBorder = false,
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const renditionClassNames = useMemo(
    () =>
      classNames(
        'sb-transparent-rendition',
        Array.isArray(borders)
          ? borders.map((border) => `sb-rendition_border-${border}`)
          : `sb-rendition_border-${borders}`,
        {
          'sb-rendition_border-rounded': roundedBorder,
          'sb-rendition_elevated': elevated,
          'sb-rendition_full-size': fullSize,
        },
        className,
      ),
    [className, elevated, fullSize, roundedBorder, borders],
  );

  return (
    <div {...otherProps} className={renditionClassNames} {...testMetadata}>
      {children}
    </div>
  );
};

export default TransparentRendition;
